@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,400;0,500;0,700;1,400;1,500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background-color: #faf1ec;
}

.button {
    width: 10em;
    font-size: 24px;
    background-color: #e9b1a4;
    border-radius: 10px;
    padding: 0.6em 1em;
    color: #455a5c;
    font-weight: 500;
}

.button:active {
    background-color: #e69f8f;
}

.button:hover {
    color: #faf1ec;
}
.containerQuote {
    width: 60%;
    border: 3px solid #4eabb2;
    border-radius: 10px;
    padding: 1em 2em;
    margin-top: 2em;
}

.divTextQuote {
    display: flex;
    flex-direction: column;
}

.quoteText {
    font-weight: 500;
    font-style: italic;
    font-size: 28px;
}

.quoteAuthor {
    align-self: flex-end;
    font-size: 18px;
    margin-top: 0;
}
.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: 'Montserrat', sans-serif;
    color: #455a5c;
    text-align: center;
    margin: 1em 2em;
}

.logo {
    margin-top: 1em;
}

.motto {
    font-size: 25px;
    font-weight: 500;
    margin-top: 0;
}

.instructions {
    font-size: 18px;
    margin-top: 2.5em;
}
